@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,500;1,100&display=swap);
.todos__list {
  margin: 0 auto;
  width: 80%;
}

.todos__list ul {
  margin: 0 auto;
  width: 100%;
  padding: 0;
}

.todo__card {
  border: 2px solid rgb(245, 239, 239);
  border-radius: 25px;
  list-style: none;
  margin: 0 auto;
  margin-bottom: 20px;
}

.todo__card li {
  width: 100%;
}

.todo__title {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}

.todo__title * {
  padding: 0;
}

label {
  cursor: pointer;
}

.todo__title button {
  width: 0;
  height: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-right: 25px;
  padding-top: -10px;
  padding-bottom: 22px;
}

.todo__heading.undone {
  text-decoration: line-through;
}

.todo__add {
  margin: 35px auto;
  margin-bottom: 60px;
  width: 80%;
  background-color: transparent;
}

.todo__form {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.todo__select {
  display: flex;
  flex-direction: column;
}

.todo__select select {
  width: 170px;
  height: 30px;
  border: none;
}

.todo__choose-user {
  font-size: 12px;
  color: black;
  letter-spacing: 3px;
  margin-top: 0;
  margin-bottom: 0;
  display: none;
}

.todo__choose-user.unchosen {
  display: inline;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.todo__text-input {
  width: 60%;
  display: flex;
  flex-direction: column;
}

.todo__add-todo {
  font-size: 12px;
  color: black;
  letter-spacing: 3px;
  margin-top: 0;
  margin-bottom: 0;
  display: none;
}

.todo__add-todo.unchosen {
  display: inline;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.todo__text-input input {
  border: none;
  background-color: transparent;
  border-bottom: 2px solid rgb(245, 239, 239);
  width: 100%;
  height: 100%;
}

.todo__text-input input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
  font-style: italic;
  font-family: "Jost", sans-serif;
}

.todo__text-input input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
  font-style: italic;
  font-family: "Jost", sans-serif;
}

.todo__text-input input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
  font-style: italic;
  font-family: "Jost", sans-serif;
}

.todo__text-input input::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
  font-style: italic;
  font-family: "Jost", sans-serif;
}

.todo__text-input input[type="text"] {
  padding-left: 20px;
  font-family: "Jost", sans-serif;
  font-size: 20px;
  color: white;
  font-style: italic;
}

.todo__button button {
  width: 40px;
  height: 40px;
  border-radius: 25px;
  border: white;
  background-color: rgb(245, 239, 239);
  cursor: pointer;
}

.todo__button button:hover {
  border: 1px solid;
  padding: 10px;
  box-shadow: 0 0 10px 5px rgb(245, 239, 239);
}

.todo__button span {
  font-size: 20px;
}

html,
body {
  font-family: "Jost", sans-serif;
  margin: 0;
}

body {
  background-color: rgba(149, 190, 206, 0.7);
}

.App {
  text-align: center;
  background-color: transparent;
  width: 60%;
  height: auto;
  margin: 0 auto;
}

.App__heading {
  color: white;
  font-size: 32px;
  border-top: 3px solid white;
  width: 13%;
  margin: 0 auto;
  margin-top: 30px;
}

